import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { store, swithLanguage } from "../../store";
import Logo from "../../assets/images/logo.png";
import { useDispatch } from "react-redux";

export const Header = () => {
  const [basketCount, setBasketCount] = useState(0);
  const basketSelector = store.getState().basket;

  store.subscribe(() => setBasketCount(store.getState().basket.length));
  useEffect(() => {
    setBasketCount(basketSelector.length);
  }, []);
  const dispatch = useDispatch();
  const currentLang = store.getState().lang;
  useEffect(() => {
    console.log(store.getState());
  }, []);
  const handleChangeLang = () => {
    if (currentLang === "ru") {
      dispatch(swithLanguage("ua"));
    } else {
      dispatch(swithLanguage("ru"));
    }
    window.location.reload();
  };
  console.log(currentLang);
  return (
    <header className="sigma_header style-5 can-sticky">
      <div className="sigma_header-topbar d-none d-md-block">
        <div className="container-fluid">
          <div className="sigma_header-topbar-inner">
            <ul className="topbar-links">
              <li>
                <Link to="/">
                  <i className="fa fa-phone"></i>
                  +380957601400
                </Link>
              </li>
              <li>
                <Link to="/">
                  <i className="fa fa-envelope"></i>
                  sunflavour.shop@gmail.com
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div className="sigma_header-middle">
        <div className="container-fluid">
          <div className="navbar">
            <div className="sigma_logo-wrapper">
              <a className="sigma_logo" href="index.html">
                <img src={Logo} alt="logo" />
              </a>
            </div>
            <ul className="navbar-nav">
              <li className="menu-item">
                <Link to="/">Home</Link>
              </li>
              <li class="menu-item">
                <Link to="/">Blog</Link>
              </li>
              <li onClick={handleChangeLang} class="menu-item">
                <a>
                  {currentLang === "ru"
                    ? "Переключится на украинский"
                    : "Перемкнути на росiйську"}
                </a>
              </li>
            </ul>
            <div className="sigma_header-controls">
              <ul className="sigma_header-controls-inner">
                <li className="header-controls-item d-none d-sm-block">
                  <Link to="/checkout">
                    <i className="flaticon-shopping-bag"></i>
                    <span className="cart-count">{basketCount}</span>
                  </Link>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </header>
  );
};
