import React, { useEffect, useState } from "react";
import { PlaceOrder } from "../../firebase";
import { store } from "../../store";
import { CheckoutItem } from "./ChekoutItem";

export const Checkout = () => {
  const [basketCount, setBasketCount] = useState([]);
  const [totalPrice, setTotalPrice] = useState(0);
  const [basketString, setBasketString] = useState("");
  const basketSelector = store.getState().basket;

  store.subscribe(() => setBasketCount(store.getState().basket));
  useEffect(() => {
    setBasketCount(basketSelector);
  }, []);
  useEffect(() => {
    let total = totalPrice;
    let basketSTR = basketString || "";

    basketCount.forEach(
      (item) => (total = total + parseFloat(item.price) * item.count)
    );
    basketCount.forEach((item) => (basketSTR = basketSTR + " " + item.title));

    setTotalPrice(total);
    setBasketString(basketSTR);
  }, [basketCount]);

  const onSubmit = async (e) => {
    e.preventDefault();

    const form = {};
    const formData = new FormData(e.target);
    // @ts-ignore
    for (const [key, value] of formData.entries()) {
      if (value) {
        form[key] = value;
      }
    }
    form.basket = basketString;
    await PlaceOrder(form);
  };
  return (
    <div class="section-padding pb-0">
      <div class="container">
        <form onSubmit={onSubmit}>
          <div class="row">
            <div class="col-xl-7">
              <h4>Детали заказа</h4>
              <div class="row">
                <div class="form-group col-xl-12">
                  <label class="form-label">
                    Полное Имя <span class="text-danger">*</span>
                  </label>
                  <input
                    type="text"
                    placeholder="First Name"
                    name="name"
                    class="form-control"
                    required=""
                  />
                </div>

                <div class="form-group col-xl-12">
                  <label class="form-label">
                    Город <span class="text-danger">*</span>
                  </label>
                  <input
                    type="text"
                    placeholder="Город"
                    name="town"
                    class="form-control"
                    required=""
                  />
                </div>
                <div class="form-group col-xl-6">
                  <label class="form-label">
                    Номер телефона<span class="text-danger">*</span>
                  </label>
                  <input
                    type="text"
                    placeholder=" Номер телефона"
                    name="phone"
                    class="form-control"
                    required=""
                  />
                </div>
                <div class="form-group col-xl-6">
                  <label class="form-label">
                    Удобный способ оплаты <span class="text-danger">*</span>
                  </label>
                  <input
                    type="text"
                    placeholder=" Удобный способ оплаты "
                    name="sposobOplaty"
                    class="form-control"
                    required=""
                  />
                </div>

                <div class="form-group col-xl-12">
                  <label class="form-label">
                    Служба доставки <span class="text-danger">*</span>
                  </label>
                  <select class="form-control">
                    <option value="">Служба доставки</option>
                    <option value="novaPoshta">Новая почта</option>
                    <option value="intaym">Интайм</option>
                    <option value="mist">МистЭкспресс</option>
                    <option value="mejdunarodnayaDostavka">
                      Международная доставка
                    </option>
                  </select>
                </div>
                <div class="form-group col-xl-6">
                  <label class="form-label">
                    Номер отделение службы доставки{" "}
                    <span class="text-danger">*</span>
                  </label>
                  <input
                    type="text"
                    placeholder=" Отделение новой почты"
                    name="novaPoshta"
                    class="form-control"
                    required=""
                  />
                </div>
                <div class="form-group col-xl-12 mb-0">
                  <label class="form-label">Коментарий</label>
                  <textarea
                    name="comment"
                    rows="5"
                    class="form-control"
                    placeholder="Коментарий"
                  ></textarea>
                </div>
              </div>
            </div>
            <div class="col-xl-5 checkout-billing mt-4 mt-xl-0">
              <div class="table-responsive-md">
                <table class="andro_responsive-table">
                  <thead>
                    <tr>
                      <th>Product</th>
                      <th>Quantity</th>
                      <th>Total</th>
                    </tr>
                  </thead>
                  <tbody>
                    {basketCount.map((item, index) => {
                      return <CheckoutItem key={index} data={item} />;
                    })}

                    <tr class="total">
                      <td>
                        <h6 class="mb-0">Общая цена</h6>
                      </td>
                      <td></td>
                      <td>
                        <strong>{totalPrice} UAH</strong>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>

              <button type="submit" class="btn btn-block">
                Оставить заказ
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
};
